@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
*{
    margin: 0;
    scroll-behavior: smooth;
}


.app{
    min-height: 100vh;
    background-color: #39445a;
    color:white;
    padding-top:130px;
    padding-bottom: 70px;
}

@media(max-width:700px)
{
    .app{
        padding-top:70px;
    }
}

.pagetitle{
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    font-family: "Montserrat",sans-serif;
    font-size: 2vw;
    padding:4px;
    border-radius: 50px;
    color:white;
}

@media(max-width:1000px){
    .pagetitle{
        font-size: 6.4vw;
    }
}
.header{
    width:100%;
    cursor:pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    background-color: #39445A;
    font-family: "Montserrat",sans-serif;
    font-size:4vw;
    padding-bottom: 15px;
    box-shadow: 0 1px 5px black;
    color:white;
    z-index:100;
    padding-top:15px;
    /* font-style: italic; */
    /* font-weight: 600; */
}

@media (max-width:1000px)
{
    .header{
        font-size: 6vw;
    }
}
.no{
    display: flex;
    width: 100%;
    height: 60vh;
    justify-content: center;
    align-items:center;
    font-size: 30px;
    font-family: "Montserrat",sans-serif;
    
}
.pagination{
    width:"100%";
    display: flex;
    justify-content: center;
    margin-top: 10px;
}
.media{
    display: flex;
    flex-direction: column;
    width: 300px;
    margin:5px;
    background-color: #282c34;
    border-radius: 10px;
    position: relative;
    font-family: "Montserrat",sans-serif;
    margin-bottom: 10px;

}

.media:hover{
    background-color: rgb(0, 0, 0);
    color:black;
}

@media (max-width:550px)
{
    .media{
        width:46%;
    }
}

.title{
    width:100%;
    text-align:center;
    font-size: 17px;
    padding:8px 0;
    font-weight: 700;
    color:gray;
}

.subtitle{
    display: flex;
    justify-content: space-between;
    padding:0 10px;   
    padding-bottom: 4px;
    color: paleturquoise
}


.ContentModal__landscape {
  object-fit: contain;
  border-radius: 10px;
}

.ContentModal__portrait {
  display: none;
  object-fit: contain;
  border-radius: 10px;
}

.tagline {
    font-size: 25px;
  padding-bottom: 10px;
  align-self: center;
  font-style: normal;
    color:palegoldenrod

}

.ContentModal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.ContentModal::-webkit-scrollbar {
  display: none;
}

.ContentModal__about {
  padding: 10px;
  width: 95%;
  height: 90%;
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  justify-content: space-evenly;
  font-weight: 300;
}

.ContentModal__title {
  height: 12%;
  font-size: 5vw;
  display: flex;
  align-items: center;
  justify-content: center;
 
}

.ContentModal__description {
  display: flex;
  height: 40%;
  overflow-y: scroll;
  padding: 15px;
  border-radius: 20px;
  scrollbar-width: thin; /* Firefox */
  /* box-shadow: inset 0 0 5px #000000; */
  text-align: justify;
  font-style: italic !important;
  color:gainsbor;
  font-size: 20px !important;
}

.ContentModal__description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .ContentModal__landscape {
    display: none;
  }
  .ContentModal__portrait {
    display: flex;
    width: 38%;
  }
  .ContentModal {
    flex-direction: row;
    justify-content: space-around;
    padding: 10px 0;
  }
  .ContentModal__about {
    width: 58%;
    padding: 0;
    height: 100%;
  }
  .ContentModal__title {
    font-size: 3.5vw;
  }
  .ContentModal__description {
    font-size: 22px;
  }
}

.button{
    color:black !important;
    font-weight: bold !important;
}
.carouselItem {
    display: flex;
    flex-direction: column;
    object-fit: contain;
    padding: 10px;
  }
  .carouselItem__img {
    border-radius: 10px;
    margin-bottom: 5px;
    box-shadow: 0px 0px 5px black;
  }
  .carouselItem__txt{
      text-align: center;
  }
.trending{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

