.header{
    width:100%;
    cursor:pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    background-color: #39445A;
    font-family: "Montserrat",sans-serif;
    font-size:4vw;
    padding-bottom: 15px;
    box-shadow: 0 1px 5px black;
    color:white;
    z-index:100;
    padding-top:15px;
    /* font-style: italic; */
    /* font-weight: 600; */
}

@media (max-width:1000px)
{
    .header{
        font-size: 6vw;
    }
}